.content-auth {
  width: 100%;
  display: flex;
  justify-content: center;
}

.authorize {
  max-width: 544px;
  width: 100%;
  background-color: #fefefe;
  box-shadow: 5px 5px 70px rgba(173, 185, 197, 0.2);
  border-radius: 16px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 40px auto 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.authorize__title-links {
  display: flex;
  max-width: 352px;
  width: 100%;
  justify-content: space-between;
  margin: 56px auto 40px;
}

.authorize__title-link {
  color: #adb9c5;
  text-decoration: none;
  font-weight: 800;
  font-size: 24px;
  line-height: 1;
}

.authorize__title-link:hover {
  transition: 0.4s linear 0.1s;
}

.authorize__title-link_active {
  text-decoration: none;
  font-weight: 800;
  font-size: 24px;
  line-height: 1;
  color: #090a0d;
}

.form-authorize {
  max-width: 352px;
  width: 100%;
  margin: auto;
}

.form-authorize__set {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  border: none;
}

.form-authorize__field {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 28px;
}

.form-authorize__field:last-of-type {
  margin-bottom: 56px;
}

.form-authorize__input-label {
  margin-bottom: 4px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #666d7a;
  text-align: start;
}

.form-authorize__input {
  border: 1px solid #adb9c5;
  border-radius: 4px;
  height: 48px;
  padding: 0;
  padding-left: 12px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  box-sizing: border-box;
}

.form-authorize__input:hover {
  border: 1px solid #3c4352;
  transition: 0.4s linear 0.1s;
}

.form-authorize__input:focus {
  border: 1.5px solid #090a0d;
  outline: none;
}

.form-authorize__input_type_error {
  border: 1.5px solid #c93434;
}

.form-authorize__input-error {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: #c93434;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  min-height: 30px;
  text-align: start;
}

.form-authorize__enter-btn {
  border-radius: 8px;
  border: none;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.3;
  color: #e6edf4;
  padding: 12px 149px;
}

.form-authorize__enter-btn_active {
  background-color: #00a2c9;
  color: #fefefe;
  cursor: pointer;
}

.form-authorize__enter-btn_active:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
  transition: 0.4s linear 0.1s;
}
.form-authorize__enter-btn_active:focus {
  background-color: #0094b8;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.form-authorize__enter-btn_disabled {
  background-color: #11b5dd;
}

.authorize__link {
  color: #090a0d;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  align-self: center;
  margin: 44px 0 64px;
  text-decoration: none;
  border-bottom: 1px solid #090a0d;
}

.authorize__link:hover {
  color: #00a2c9;
  border-bottom: 1px solid #00a2c9;
  transition: 0.4s linear 0.1s;
}

.authorize__link:focus {
  color: #0094b8;
  border-bottom: 1px solid #0094b8;
}

@media screen and (max-width: 1023px) {
  .authorize__title-links {
    margin-top: 48px;
    margin-bottom: 40px;
    width: 63%;
  }
  .form-authorize__field:last-of-type {
    margin-bottom: 40px;
  }
  .authorize__link {
    margin-top: 36px;
    margin-bottom: 56px;
  }
}

@media screen and (max-width: 600px) {
  .authorize {
    max-width: 328px;
  }
  .authorize__title-links {
    margin-top: 40px;
    width: 85%;
  }
  .authorize__title-link {
    font-size: 22px;
  }
  .form-authorize {
    max-width: 328px;
  }
  .form-authorize__set {
    max-width: 288px;
    margin: auto;
  }
  .form-authorize__input {
    height: 40px;
  }
  .form-authorize__enter-btn {
    padding-left: 117px;
    padding-right: 117px;
  }
  .authorize__link {
    margin-bottom: 52px;
  }
}

@media screen and (max-width: 345px) {
  .authorize {
    max-width: 304px;
  }
  .authorize__title-links {
    width: 90%;
  }
}
